import React from 'react';
import KeyboardEventHandler from 'react-keyboard-event-handler';
import { CircularProgressbarWithChildren, buildStyles } from 'react-circular-progressbar';
import tools from './util.js'

import {DataService} from '../services/DataService';

class ClassBegin extends React.Component {
    constructor(props) {
        //console.log("constructor")
        super(props);
        this.resetCounter = this.resetCounter.bind(this)
        this.getExerciseWebSockit = this.getExerciseWebSockit.bind(this)

        this.app = this.props.parent;
        this.state = { 'handleKeys' : [/*'enter','left'*/],
                        'beforeStartTime' : new Date().getTime(),
                        'startTime': null,
                        'intervalCount' : 0,
                        'course':null,"countDown":6,"timer":"00:06","newtimer":"","start":false,"counter":0,
                        "isShowTip":false,'tip':[{
                                                    "name": "雙腳打開與肩同寬",
                                                    "check": false
                                                },
                                                {
                                                    "name": "雙手抓取啞呤慢慢彎曲手臂向上彎舉",
                                                    "check": true
                                                }],
                        "isHold":false,"holdSec":2,"currentHold":0,"startHoldTime":0,"holdSecPreson":1,
                        "isShowNotice":false, 'notice':'左肩膀移動了，請回到預備動作重新開始!',
                        "noticeHoldSec":2,"noticeCurrentHold":0,"noticeStartHoldTime":0,"noticeHoldSecPreson":1,
                        "isRest": false, "defaultResetSec": 30,"currentReset":0, "ResetSecPreson":1, "startResetTime":null,
                        "quit_time" : null,
                        "defaultSets":this.app.state.defaultSets,
                        "actionSets":1,
                        "actionCountDown": this.app.state.actionCountDown,//12,
                        "defaultCountDown": this.app.state.defaultCountDown,//12,
                        "stopSecPreson" : 0 ,"stopSec":0,
                        "responseBody": null}
        this.props.parent.currentPage = this;
        this.props.parent.setState({"availableKeys" : this.state.handleKeys})

        this.percentage = 30
    }

    async gotoFirstPage(page){
        this.beforeExistPage();
        //console.log( "gotoFirstPage...");
        let user = this.app.state.user;
        let log = this.props.parent.state.log;
        log['end_time'] = tools.Util.DateTimeFormat(new Date(),"yyyy/MM/dd hh:mm:ss")
        log['elapsed_time'] = ( Date.parse(log.end_time) - Date.parse(log.start_time) ) / 1000
        log['page'] = page;
        log['course'] = user.course.title

        if (this.app.state.haveCourseSet !== undefined){
            this.app.setState({courseSetExist:true})
            this.app.changePage( this.app.state.pageIndex.CourseSet )

        } else {

            setTimeout( ()=>{
                this.app.changePage( this.app.state.pageIndex.Home )
            },500)

            tools.Util.sendLogNoScore(log);
        }
    }

    async gotoNextPage(){
        this.beforeExistPage();
        //console.log( "gotoNextPage...");
        let user = this.app.state.user;
        user['timer'] = this.state.timer;
        user['end_time'] = tools.Util.DateTimeFormat(new Date(),"yyyy/MM/dd hh:mm:ss")
        user['elapsed_time'] = ( Date.parse(user.end_time) - Date.parse(user.start_time) ) / 1000
        //user['correct_rate'] = 99
        //console.log( "user...",user);
        this.app.setState({"user":user})
        //console.log( this.app.state.haveCourseSet, this.app.state.haveCourseSet!= null)
        if (this.app.state.haveCourseSet !== undefined){
            //console.log( user.count, this.state.defaultCountDown)
            this.app.setState({courseSetExist: user.count < this.state.defaultCountDown })
            setTimeout( ()=>{
                this.app.changePage( this.app.state.pageIndex.CourseSet )
            },500)
        } else {
            //console.log("user.count",user.count);
            //console.log(this.app)
            if (user.count > 0)
                setTimeout( ()=>{
                    this.app.changePage( this.app.state.page+1 )
                },500)
            else {
                let log = this.props.parent.state.log;
                log['end_time'] = tools.Util.DateTimeFormat(new Date(),"yyyy/MM/dd hh:mm:ss")
                log['elapsed_time'] = ( Date.parse(log.end_time) - Date.parse(log.start_time) ) / 1000
                log['page'] = 'Page 3';
                log['course'] = user.course.title

                setTimeout( ()=>{
                    this.app.changePage( this.app.state.pageIndex.Class2 )
                },500)
                tools.Util.sendLogNoScore(log);
            }
        }
    }

    resetCounter(){
    }

    getExerciseWebSockit(value){
        this.setState({"responseBody":value})
    }

    async componentDidMount() {
        //console.log( "ClassBegin ...componentDidMount...")
        window.setElementEventLintener("ClassBegin")
        let self = this;
        var course = await DataService.getCourse(this.app.state.coachid,this.app.state.cid,this.app.state.ciid);
        if (this.props.parent.state.isDeveMode === false){
            this.app.ws.send( JSON.stringify({"function":"uploadCourse", "name":encodeURI(course.title)} ))
        }
        this.setState({"course": course ,
                       "countDown":3,"timer":"00:03","start":false})

        this.app.setState({"user":{
            "course" : course,
            'count' : 0,
            'countTimes' : [],
            'score' : 0,

            'coach': course.coach.name,
            'category': course.category.name,
            'userId' : this.app.state.user.userId,
            "age": this.app.state.user.age,
            "gender": this.app.state.user.gender,
            "height": this.app.state.user.height,
            "weight": this.app.state.user.weight,
            "accuniq": this.app.state.user.accuniq,
        }})


        this.video.onplaying = function(event) {
            if (self.state.start === false || (self.state.start === true && self.state.isRest === true) )
                event.target.pause();
            let videoHeight = event.target.videoHeight;
            let videoWidth = event.target.videoWidth;
            event.target.setAttribute("style",'left:'+  (( 1080 - (1450*videoWidth/videoHeight) ) / 2) +'px; ')
        };

        if (this.video2 != null){
            this.video.onended = function(event) {
                window.$('#videoplayer')[0].currentTime = self.state.course.video_skip;
                window.$('#videoplayer2')[0].currentTime = self.state.course.second_video_skip;
                window.$('#videoplayer')[0].play()
                window.$('#videoplayer2')[0].play()
            };

            this.video2.onplaying = function(event2) {
                if (self.state.start === false || (self.state.start === true && self.state.isRest === true) )
                    event2.target.pause();
                let videoHeight2 = event2.target.videoHeight;
                let videoWidth2 = event2.target.videoWidth;
                event2.target.setAttribute("style",'left:'+  (( 1080 - (1450*videoWidth2/videoHeight2) ) / 2) +'px; ')
            };

            this.video2.onended = function(event) {
                window.$('#videoplayer')[0].currentTime = self.state.course.video_skip;
                window.$('#videoplayer2')[0].currentTime = self.state.course.second_video_skip;
                window.$('#videoplayer')[0].play()
                window.$('#videoplayer2')[0].play()
            };
        }

        var interval = window.setInterval(async()=>{
            let canvaies = window.$.find('canvas');
            if (canvaies.length>0){
                canvaies.forEach(function(value){
                    value.remove();
                });
            }

            let responseBody = null
            let responseState = false;

            responseBody = this.state.responseBody
            responseState = responseBody === null;


            let nowTime = new Date().getTime()

            if (responseState){

                if ((nowTime - this.state.beforeStartTime) >= 30 * 1000) {
                    if (this.props.parent.state.isDeveMode === false){
                        this.gotoFirstPage('Page 3 no response');
                    }
                } else {
                    this.setState({"stopSecPreson": (nowTime - this.state.beforeStartTime)/1000/30 })
                    this.setState({"stopSec": Math.ceil( (nowTime - this.state.beforeStartTime)/ 1000) })
                    if (this.props.parent.state.isDeveMode === true){

                        if (Math.ceil( (nowTime - this.state.beforeStartTime)/ 1000) > 10 && this.video.paused){
                            this.setState({start:true})

                            if (this.video2 != null){
                                window.$('#videoplayer')[0].currentTime = this.state.course.video_skip;
                                window.$('#videoplayer2')[0].currentTime = this.state.course.second_video_skip;
                                this.video.play();
                                this.video2.play();

                            } else {
                                this.video.play();
                            }
                        }
                    }
                }

                /*if ((nowTime - this.state.beforeStartTime) >= 10 * 1000) {
                    this.gotoNextPage();
                    this.beforeExistPage();
                    window.$('.testAnimation').css('display','block');
                    window.$('.testAnimation').addClass('testAnimation_animation')
                    //animation: clipcircle 1s linear 0S infinite;
                    setTimeout( ()=>{
                        this.app.changePage( this.app.state.page+1 )
                    },500)
                }*/

            } else {
                let doHold = this.state.isHold;
                let doNoticeHold = this.state.isShowNotice;

                let startHoldTime = this.state.startHoldTime;
                let noticeStartHoldTime = this.state.noticeStartHoldTime;
                let currentHoldTime = new Date().getTime();

                let doRest = this.state.isRest;

                var value = responseBody

                //console.log( "quit", value.action.quit );
                if (value.action.quit){
                    let lastTime = nowTime
                    if (this.state.quit_time != null){
                        lastTime = this.state.quit_time
                    } else {
                        this.setState({"quit_time" : nowTime})
                    }

                    doNoticeHold = true;
                    let alertMessage = "三秒後離開"
                    this.setState({"isShowNotice": true, "notice": alertMessage,
                        "noticeStartHoldTime":nowTime,"noticeCurrentHold":2})

                    if ((nowTime - lastTime) >= 3 * 1000) {

                        this.app.setState({'courseQuit':true})

                        this.gotoNextPage();
                    }
                } else {
                    this.setState({"quit_time" : null})
                    if (doNoticeHold){
                        if (this.state.notice === "三秒後離開"){
                            doNoticeHold = false;
                            this.setState({"isShowNotice": false});
                        }
                    }
                }

                if ( !value.start ){ // 姿勢設定
                    this.setState({"isShowTip": (value.tip.note.length > 0),
                                "tip" : value.tip.note,
                                "holdSec": value.tip.duration})

                    let lastTime = (value.action.startPointLastTime !== "" )
                        ? Date.parse(value.action.startPointLastTime)
                        : this.state.beforeStartTime
                    if ((nowTime - lastTime) >= 30 * 1000 && doRest===false) {
                        console.log( "response not ok.....30秒")
                        this.gotoFirstPage('Page 3 not Start ')

                    } else {
                        this.setState({"stopSecPreson": (nowTime - lastTime)/1000/30 })
                        this.setState({"stopSec": Math.ceil( (nowTime - lastTime)/ 1000) })
                    }

                } else { //開始倒數
                    this.setState({"isShowTip": false})
                    if (this.state.startTime == null)
                        this.setState({"startTime":nowTime})

                    if (this.state.intervalCount % 10 === 0){
                        if ( this.state != null && this.state.course != null ){
                            var newCountDown = this.state.countDown

                            if (this.state.start === false){
                                newCountDown = newCountDown-1
                                this.setState({"stopSecPreson":0, "stopSec": 0});

                                if (newCountDown === 0){
                                    this.setState({ "start":true})

                                    let user = this.app.state.user;
                                    user['start_time'] = tools.Util.DateTimeFormat(new Date(),"yyyy/MM/dd hh:mm:ss")
                                    this.app.setState({"user":user})
                                    this.transition.setAttribute("style",'display:block; ')
                                    this.transitionImg.setAttribute("src",'')
                                    this.transitionImg.setAttribute("src","./assets/start.gif")
                                    this.transitionImg.setAttribute("style",'display:block; ')
                                }
                            }else{
                                this.transition.setAttribute("style",'display:none; ')
                                this.transitionImg.setAttribute("src",'')
                                this.transitionImg.setAttribute("style",'display:none; ')
                                newCountDown = newCountDown+1
                            }
                            newCountDown = newCountDown < 0 ? 0  : newCountDown

                            var h = parseInt( Math.abs(newCountDown) / 3600 )
                            var m = parseInt( Math.abs(newCountDown) / 60 )
                            var s = Math.abs(newCountDown) % 60
                            var timer = (h > 0 ? (h<10 ? "0"+h : h )+":"  :'')+(m<10 ? "0"+m : m ) +":"+( s< 10 ?"0"+s : s)
                            this.setState({ "timer": timer, "countDown": newCountDown, "newtimer":s})

                        }
                    }

                    if (this.state.start){
                        if (this.video.paused && self.state.isRest === false){
                            if (this.video2 != null){
                                window.$('#videoplayer')[0].currentTime = this.state.course.video_skip;
                                window.$('#videoplayer2')[0].currentTime = this.state.course.second_video_skip;
                                this.video.play();
                                this.video2.play();
                            } else {
                                this.video.play();
                            }
                        }

                        let user = this.app.state.user;
                        let actionTimes = value.action.times - ( (this.state.actionSets-1) * this.state.defaultCountDown);

                        this.setState({"actionCountDown" : this.state.defaultCountDown - (actionTimes < 0 ? 0: actionTimes)})

                        if (value.action.times > user['count']){
                            user['count'] = value.action.times;
                            let beginTime = Date.parse(value.action.startPoint)
                            let endTime = Date.parse(value.action.startPointLastTime)
                            user['countTimes'].push(endTime - beginTime)
                            user['score'] = value.action.score

                            this.app.setState({"user":user})

                            if ( this.state.defaultCountDown - actionTimes === 0){
                                console.log("完成 "+this.state.defaultCountDown+" 次..., 第 "+ this.state.actionSets+"/"+this.state.defaultSets+" 組")

                                //console.log( this.app.state.haveCourseSet , this.app.state.courseSetLength, this.app.state.courseSetIndex )
                                if (this.app.state.haveCourseSet === undefined){
                                    this.gotoNextPage();
                                } else {
                                    if ( this.app.state.courseSetLength === this.app.state.courseSetIndex && this.state.actionSets === this.state.defaultSets ){
                                        this.gotoNextPage();
                                    } else {
                                        let value2 = JSON.parse(JSON.stringify(value))
                                        let restTime = Date.parse(value2.action.startPointLastTime);
                                        restTime = restTime + this.state.defaultResetSec*1000

                                        value2.action.startPointLastTime = tools.Util.DateTimeFormat( new Date(restTime),"yyyy/MM/dd hh:mm:ss")

                                        this.setState({"actionSets": this.state.actionSets+1,
                                                       "isRest": true, "startResetTime": currentHoldTime,"responseBody":value2})
                                        this.app.setState({"actionSets": this.app.state.actionSets+1})

                                        doRest = true;
                                        if (this.video2 != null){
                                            this.video.pause();
                                            this.video2.pause();
                                        } else {
                                            this.video.pause();
                                        }

                                        if (this.props.parent.state.isDeveMode === false){
                                            this.app.ws.send( JSON.stringify({"take_a_break":true} ))
                                        }
                                    }
                                }
                            }
                        }

                        if (value.action.stop === true){
                            this.setState({"isHold":false})
                            doHold = false;
                        }

                        if (value.action.startPoint !== "" ){
                            let lastTime = Date.parse(value.action.startPoint)
                            if ((nowTime - lastTime) < 0.5 * 1000) {
                                this.setState({"stopSecPreson":0, "stopSec": 0});

                                doHold = true;
                                doNoticeHold = false;
                                this.setState({"isShowNotice": false,
                                        "isHold":true,
                                        "startHoldTime":nowTime,"currentHold":this.state.holdSec})
                                currentHoldTime = nowTime;
                            }
                        }

                        if (doRest === false){
                            if (value.action.startPointLastTime !== "" ){
                                let lastTime = Date.parse(value.action.startPointLastTime)

                                if ((nowTime - lastTime) < 0.5 * 1000) {
                                    let alertMessage = '';
                                    value.action.alert.forEach(function(val){
                                        alertMessage += ( alertMessage.length>0 ? '<br/>' : '') +val;
                                    });

                                    doNoticeHold = true;
                                    this.setState({"isShowNotice": true, "notice": alertMessage,
                                    "noticeStartHoldTime":nowTime,"noticeCurrentHold":2})
                                }

                                if ((nowTime - lastTime) >= 30 * 1000  && doRest===false) {
                                    console.log("超過 30秒...")
                                    this.gotoNextPage();
                                } else {
                                    this.setState({"stopSecPreson": (nowTime - lastTime)/1000/30 })
                                    this.setState({"stopSec": Math.ceil( (nowTime - lastTime)/ 1000) })
                                }
                            } else {
                                if ((nowTime - this.state.startTime) >= 30 * 1000  && doRest===false) {
                                    console.log( "No action any one .....30秒")

                                    this.gotoFirstPage('Page 3 no action any more ')
                                } else {
                                    this.setState({"stopSecPreson": (nowTime - this.state.startTime)/1000/30 })
                                    this.setState({"stopSec": Math.ceil( (nowTime - this.state.startTime)/ 1000) })
                                }
                            }
                        }
                    }

                    if (doRest){
                        let _holdSec = this.state.defaultResetSec*1000
                        let step1 = this.state.startResetTime + _holdSec;
                        let step2 = step1 - currentHoldTime;
                        let step3 = step2 / _holdSec
                        this.setState({"currentReset" : Math.ceil( step2/ 1000),
                                    "ResetSecPreson": step3,"isRest":step3 > 0})

                        if ( (step3 > 0) === false){
                            if (this.props.parent.state.isDeveMode === false){
                                this.app.ws.send( JSON.stringify({"take_a_break":false} ))
                            }
                            //console.log( this.state.defaultSets, this.state.actionSets-1)
                            if ( this.state.defaultSets === this.state.actionSets-1){
                                this.gotoNextPage();
                            } else {
                                if (this.video2 != null){
                                    window.$('#videoplayer')[0].currentTime = this.state.course.video_skip;
                                    window.$('#videoplayer2')[0].currentTime = this.state.course.second_video_skip;
                                    this.video.play();
                                    this.video2.play();
                                } else {
                                    this.video.play();
                                }
                            }
                        }
                                    //"isRest": false, "defaultResetSec": 30,"currentReset":0, "ResetSecPreson":1,
                    }


                    if (doHold){
                        let _holdSec = this.state.holdSec*1000
                        let step1 = startHoldTime + _holdSec;
                        let step2 = step1 - currentHoldTime;
                        let step3 = step2 / _holdSec
                        this.setState({"currentHold" : Math.ceil( step2/ 1000),
                                    "holdSecPreson": step3,"isHold":step3 > 0})
                    }

                    if (doNoticeHold){
                        let _holdSec = this.state.noticeHoldSec*1000
                        let step1 = noticeStartHoldTime + _holdSec;
                        let step2 = step1 - nowTime;
                        let step3 = step2 / _holdSec
                        this.setState({"isShowNotice":step3 > 0, "isHold":false})
                    }

                    this.setState({'intervalCount':this.state.intervalCount+1})
                }
            }
            if (this.video2 != null ){
                let sec =  Math.round( (nowTime - this.state.beforeStartTime)/ 1000) % 10;
                if ( sec === 5 ){
                    window.$('#videoplayer').hide()
                    window.$('#videoplayer2').show()
                } else if ( sec === 0){
                    window.$('#videoplayer2').hide()
                    window.$('#videoplayer').show()
                }
            }
        }, 100)
        this.setState({"interval":interval})

        this.app.setState({"doExercise":true})
    }

    //async componentWillUnmount(){
    async beforeExistPage(){
        this.app.setState({"doExercise":false})

        window.clearInterval( this.state.interval )
        if (this.props.parent.state.isDeveMode === false){
            this.app.ws.send( JSON.stringify({"function":"endExercise"} ))
        }

        window.$('.testAnimation').css('display','block');
        window.$('.testAnimation').addClass('testAnimation_animation')
    }


    render(){
        return (<KeyboardEventHandler
                handleKeys={this.state.handleKeys}
                onKeyEvent={(key, e, page) => this.props.util(key, e, this, page)} >
                <div style={{backgroundImage:'url(./assets/v_bg.jpg)', width:1080, height:1920}}>
                    <div className="classBegin_backgroundImg" ></div>

                    <div className="chanetransition"
                        ref={(input) => { this.transition = input; }} ></div>
                    <div className="classBegin_ClassName" style={{color: this.state != null && this.state.course != null ? this.state.start ? "#d4d4d4" : "#74E0FC" : "#74E0FC"}}>
                    { this.state != null && this.state.course != null ? this.state.start ? this.state.course.title : "<請就預備姿勢>" : null}
                    </div>
                    <div className="classBegin_Timer">
                        { this.state != null && this.state.timer != null ? this.state.start ? this.state.timer : "" : null}
                    </div>
                    <div className="classBegin_newTimer">{ this.state != null && this.state.newtimer != null ? !this.state.start ? this.state.newtimer : "" : null}</div>

                    <div className="classBegin_circleProgress"
                        style={{display: this.state.isHold ? 'none'/*'block'*/ : 'none' }}
                    ref={(input) => { this.circleprogress = input; }}
                    >
                        <CircularProgressbarWithChildren
                            value={ parseInt(this.state.holdSecPreson * 100 )}
                            background
                            styles={buildStyles({
                                strokeLinecap: "butt",
                                pathColor: '#17FF21',
                                trailColor: 'rgba(214, 214, 214, 0.6)',
                                backgroundColor: 'rgba(0, 0, 0, 0.7)',
                            })}
                            strokeWidth={5}
                            >
                            <div className="classBegin_circleProgress_title">動作</div>
                            <div className="classBegin_circleProgress_value">{this.state.currentHold}</div>
                            <div className="classBegin_circleProgress_sec">秒</div>
                        </CircularProgressbarWithChildren>
                    </div>

                    <div className="classBegin_stopCircleProgress" style={{display:'none'}}>
                    <CircularProgressbarWithChildren
                            value={ parseInt(this.state.stopSecPreson * 100 ) }

                            styles={buildStyles({
                                strokeLinecap: "butt",
                                pathColor: '#17FF21',
                                trailColor: 'rgba(214, 214, 214, 0.6)',
                            })}
                            strokeWidth={5}
                            >

                            <div className="classBegin_stopCircleProgress_value">{this.state.stopSec}</div>

                        </CircularProgressbarWithChildren>
                    </div>

                    <div className="classBegin_counter" style={{display: this.state.start ? 'block' : 'none' }}></div>
                    <div className="classBegin_SET_Title" style={{display: this.state.start ? 'block' : 'none' }}>剩餘次數</div>
                    <div className="classBegin_SET_Value" style={{display: this.state.start ? 'block' : 'none' }}>{this.state.actionCountDown}</div>

                    <img className="classBegin_StartImg" src=""
                        style={{display:'none'}}
                        ref={(input) => { this.transitionImg = input; }} alt=""></img>


                    { (this.state != null && this.state.course != null && this.state.course.second_video == null)
                        ?
                        <video id="videoplayer" controls={false}  loop={true} autoPlay={true} className="classBegin_video" muted="muted" ref={(video) => { this.video = video; }}>
                            <source src={this.state != null && this.state.course != null ? '/course'+this.state.course.video : null} />
                        </video>
                        :
                        <video id="videoplayer" controls={false} autoPlay={true} className="classBegin_video" muted="muted" ref={(video) => { this.video = video; }}>
                            <source src={this.state != null && this.state.course != null ? '/course'+this.state.course.video : null} />
                        </video>
                    }

                    {
                        (this.state != null && this.state.course != null && this.state.course.second_video != null) ?
                    <video id="videoplayer2" controls={false} autoPlay={true} className="classBegin_video2" muted="muted" ref={(video2) => { this.video2 = video2; }}>
                        <source src={this.state != null && this.state.course != null ? '/course'+this.state.course.second_video : null} />
                    </video>
                        :null
                    }

                    <div className="classBegin_HoldImg" style={{display: this.state.isRest ? 'block' : 'none' }}>
                        <div className="classBegin_HoldImg_Title">休息時間</div>
                        <div className="classBegin_HoldImg_value_area" >
                        <div className="classBegin_HoldImg_Value">{this.state.currentReset}</div><div className="classBegin_HoldImg_Sec">秒</div>
                        </div>
                        <div className="classBegin_HoldImg_progressBar_base" >
                            <div className="classBegin_HoldImg_progressBar_value" style={{width: 597 * this.state.ResetSecPreson }}></div>
                        </div>
                    </div>

                    <div className="classBegin_noticeboard" style={{display: this.state.isShowNotice ? 'block' : 'none' }}>
                        <div className="classBegin_noticeboard_title">{this.state.notice}</div>
                    </div>

                    <div className="classBegin_actionTip" style={{display: this.state.isShowTip ? 'block' : 'none' }}>
                        <div className="classBegin_actionTip2" >
                            <div className="classBegin_actionTip_img"></div>
                            <div className="classBegin_actionTip_text">
                                <div>
                                {
                                (this.state.tip.length > 0)
                                ?
                                this.state.tip.map( (tip_value, index )=>{
                                    return <span style={{display:'block',paddingLeft:75,color:( tip_value.check ? '#17FF21' : '#D4D4D4'),marginBottom: 16}} key={index}>
                                            {(tip_value.check)
                                            ? <i className="classBegin_checked"/>
                                            : <i className="classBegin_unchecked"/>
                                            }
                                            {index+1}.{tip_value.name}</span>
                                })
                                : null
                                }</div>
                            </div>
                        </div>
                    </div>

                    <div className="classBegin_QuickImage" style={{display: this.state.start ? 'block' : /*'none'*/'block' }}></div>
                    <div className="classBegin_QuickTextArea" style={{display: this.state.start ? 'block' : /*'none'*/'block' }}><div style={{color:"#17FF21"}}>離開</div>左手45度</div>
                </div>
                <div className="testAnimation"></div>
            </KeyboardEventHandler>)
    }
}

export default ClassBegin;
