import React from 'react';

import {DataService} from '../services/DataService';

class CourseSet extends React.Component {
    constructor(props) {
        super(props);
        this.app = this.props.parent;
    }

    resetCounter(){
    }

    async componentDidMount() {
        if (this.app.state.haveCourseSet !== undefined){
            var courseSet = await DataService.getCourseSet(this.app.state.haveCourseSet[0].course_set_id)
            this.app.setState({"courseSetLength":courseSet.courses.length})
            let index = this.app.state.courseSetIndex;
            let user = this.app.state.user;
            let userSet = this.app.state.userSet || [];
            if ( index >= 1){
                if (index === 1 && user.count === 0){
                    this.app.changePage( this.app.state.pageIndex.Home )
                    return;
                }

                userSet.push( JSON.parse(JSON.stringify(user)) )
                this.app.setState({userSet:userSet});
            }

            //console.log("userSet",userSet)
            //console.log( "courseSetExist", this.app.state.courseSetExist, courseSet.courses.length > index )
            if (this.app.state.courseSetExist === false && courseSet.courses.length > index && this.app.state.courseQuit == false){
                //console.log("doChangePage ")
                this.app.setState({"courseSetIndex": index+1, "cid": courseSet.courses[index].category_id, "ciid": courseSet.courses[index].course_id,
                                    defaultSets: courseSet.courses[index].sets,

                                    actionCountDown:courseSet.courses[index].reps ,
                                    defaultCountDown:courseSet.courses[index].reps ,
                            });
                this.app.changePage( this.app.state.pageIndex.ClassBegin )
            } else {
                //console.log("do exist course ")
                user["course"] = {title:courseSet.name};
                user['start_time'] = userSet[0]['start_time']
                user['end_time'] = userSet[ userSet.length -1 ]['end_time']
                user['elapsed_time'] = ( Date.parse(user.end_time) - Date.parse(user.start_time) ) / 1000
                user['count'] = 0
                user['countTimes'] = []
                user['score'] = 0
                let _time = [0,0]
                //console.log( "userSet", JSON.stringify(userSet,null,4) )
                for(let i=0;i<userSet.length;i++){
                    user['count'] = user['count'] + userSet[i]['count'];
                    user['score'] = user['score'] + userSet[i]['score'];
                    user['countTimes'] = user['countTimes'].concat(userSet[i]['countTimes'])

                    let _timeTemp = userSet[i]['timer'].split(":")
                    _time[0] = _time[0]+parseInt(_timeTemp[0])
                    _time[1] = _time[1]+parseInt(_timeTemp[1])
                }

                user['score'] = Math.round( user['score'] / userSet.length * 10 ) / 10

                _time[0] = _time[0] + parseInt(_time[1] / 60)
                _time[1] = Math.round( (_time[1] % 60) * 10) / 10

                user['timer'] = _time[0]+":"+_time[1];

                this.app.changePage( this.app.state.pageIndex.ClassDone )
            }
        } else {
            this.app.changePage( this.app.state.pageIndex.Class2 )
        }
    }

    componentWillUnmount(){

    }

    render(){
        return (<div></div>)
    }
}

export default CourseSet;
